import React, { useState, useEffect } from "react";
import Header from "./Conponents/Navbar/Navbar";
import Home from "./Conponents/Home/Home";
import TexteAccueil from "./Conponents/TexteAccueil/TexteAccueil";
import Feature from "./Conponents/Feature/Feature";
import Discover from "./Conponents/Discover/Discover";
import Area from "./Conponents/Area/Area";
import Questions from "./Conponents/Questions/Question";
import Contact from "./Conponents/Contact/Contact.jsx";
import Footer from "./Conponents/Footer/Footer";
import "bootstrap/dist/css/bootstrap.min.css";
import { ThemeProvider, createGlobalStyle } from "styled-components";
import storage from "local-storage-fallback";
const GlobalStyle = createGlobalStyle`
body{
  background-color: ${(props) =>
    props.theme.mode === "dark" ? "#191970" : "#EEE"};
     color: ${(props) => (props.theme.mode === "dark" ? "#EEE" : "#677294")}   
    }
 
  }  
    `;
function getInitialTheme() {
  const saveTheme = storage.getItem("theme");
  return saveTheme ? JSON.parse(saveTheme) : { mode: "light" };
}

function App() {
  const [theme, setTheme] = useState(getInitialTheme);

  useEffect(() => {
    storage.setItem("theme", JSON.stringify(theme));
  }, [theme]);

  return (
    <ThemeProvider theme={theme}>
      <GlobalStyle />
      <Header changeWord={(word) => setTheme((theme.mode = { mode: word }))} />
      <Home />
      <TexteAccueil />
      <Feature />
      <Discover />
      <Area />
      <Questions />
      <Contact />
      <Footer />
    </ThemeProvider>
  );
}

export default App;
